import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import { BehaviorSubject } from 'rxjs';
import {
  map, filter, catchError, mergeMap,
} from 'rxjs/operators';
import { MenuMaster } from './menu-master';

@Injectable({
  providedIn: 'root'
})
export class MenuMasterService {
  private baseUrl: string = 'http://localhost:8080/api';

  private headers = new Headers({ 'Content-Type': 'application/json' });

  private options = new RequestOptions({ headers: this.headers });
  dialogData: any;

  dataChange: BehaviorSubject<MenuMaster[]> = new BehaviorSubject<MenuMaster[]>([]);

  private menuMaster: MenuMaster;

  constructor(private _http: Http) { }

  get data(): MenuMaster[] {
    return this.dataChange.value;
  }

  getDialogData() {
    return this.dialogData;
  }

  setter(menuMaster: MenuMaster) {
    this.menuMaster = menuMaster;
  }

  getter() {
    return this.menuMaster;
  }

  getMenuMaster(){
    return this._http.get(this.baseUrl+'/getMenuMaster',this.options)
    .pipe(map((response:Response)=>response.json()),
  catchError((error:any)=>{
    return Observable.throw(error);
  }));
  }
  saveMenuMaster(menuMaster:MenuMaster){
    return this._http.post(this.baseUrl+'/saveMenuMaster',JSON.stringify(menuMaster),this.options)
    .pipe(map((response:Response)=>response.json()),
  catchError((error:any)=>{
    return Observable.throw(error);
  }));
  }

  updateMenuMaster(menuMaster:MenuMaster){
    return this._http.put(this.baseUrl+'/updateMenuMaster',JSON.stringify(menuMaster),this.options)
    .pipe(map((response:Response)=>response.json()),
  catchError((error:any)=>{
    return Observable.throw(error);
  }));
  }

  deleteMenuMaster(menuId:number){
    return this._http.delete(this.baseUrl+'/deleteMenuMaster/'+menuId,this.options)
    .pipe(map((response:Response)=>response.json()),
  catchError((error:any)=>{
    return Observable.throw(error);
  }));
  }
  getMenuDetails(menuName:string){
    return this._http.get(this.baseUrl + '/getMenuDetails/' +menuName, this.options)
    .pipe(map((response: Response) => response.json()),
      catchError((error: any) => {
        return Observable.throw(error);
      }));
  }
}
